
.hero{
    display: flex;
    justify-content:space-between;  
}

.left-h{
    padding: 2rem;
    padding-top: 1.5rem;
    flex:3;
    display: flex;
    gap: 2rem;
    flex-direction: column;
}
.right-h{
    flex: 1;
    position: relative;
    background-color:var(--orange);
    height: 100vh;
}
.ad{
    margin-top: 4rem;
    background-color:#363d42 ;
    border-radius: 4rem;
    width:fit-content;
    padding: 20px 13px;
    text-transform: uppercase;
    color: white;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.ad>div{
    position: absolute;
    background-color: var(--orange);
    width: 5.4rem;
    height: 80%;
    left: 8px;
    border-radius: 3rem;
    z-index: 1;

}
.ad>span{
    z-index: 3;
}

.hero-text{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    text-transform: uppercase;
    font-size: 4.5rem;
    font-weight: bold;
    color: white;
    text-overflow: inherit;
      
}
.hero-text>div:nth-of-type(3){
    font-size: 1rem;  
    font-weight:200 ;
    text-transform: none;
    letter-spacing: 1px;
    width: 80% ;
}

.figures{
    display: flex;
    gap:2rem;
}
.figures>div{
    display: flex;
    flex-direction: column;
}
.figures>div>span:nth-of-type(1){
    color: white;
    font-size: 2rem;
}
.figures>div>span:nth-of-type(2){
color: white;
text-transform: uppercase;
}
.hero-btn{
    display: flex;
    gap: 1rem;
    font-weight: normal;
}
.hero-btn>:nth-child(1){
    color: white;
    background-color: var(--orange);
    width: 8rem;
}
.hero-btn>:nth-child(2){
    color: white;
    background-color: transparent;
    width: 8rem;
    border: 2px solid var(--orange);
}
.right-h>a>.btn{
    position: absolute;
    right: 5.2rem;
    top: 2rem;
    color:black;
    text-decoration: none;
    
}
.heart-rate{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: var(--darkGrey);
    width: fit-content;
    padding: 1rem;
    align-items: flex-start;
    border-radius: 5px;
    position: absolute;
    right: 4rem;
    top: 7rem;
}
.heart-rate>:nth-child(2){
    color: var(--gray);
}
.heart-rate>:nth-child(3){
    color: white;
    font-size: 1.5rem;
}
.heart-rate>img{
    width: 2rem;
}
.hero-image{
    position: absolute;
    top: 15.2rem;
    right: 9rem;
    width: 23rem;

}
.hero-image-back{
    position: absolute;
    top:4rem;
    right: 20rem;
    z-index: -1;
    width: 15rem;
}
.calories{
    display: flex;
    gap: 2rem;
    background-color: var(--caloryCard);
    border-radius: 5px;
    padding: 1rem;
    width: fit-content;
    position: absolute;
    top: 34rem;
    right: 28rem;
}
.calories>img{ 
    width:3rem;
}
.calories>div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.calories>div>:nth-child(1){
    color: var(--gray);
}
.calories>div>:nth-child(2){
    color:white;
    font-size: 1.5rem;
}
